.App {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  font-family: Arial, Helvetica, sans-serif;
}

body {
  margin: 0%;
  padding: 0%;
}
.header {
  flex: 30%;
  background-color: rgb(241, 253, 65);
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header .inputContainer {
  display: flex;
  flex-direction: column;
}

.header input {
  width: 200px;
  height: 40px;
  border: none;
  border-bottom-left-radius: 8px;
  border-top-left-radius: 8px;
  padding-left: 10px;
  margin-top: 10px;
  margin-right: 10px;
  border: 3px solid rgb(255, 94, 0);
}

.header input:focus {
  border-color: white !important; /* Change border color to white when input is focused */
  outline: none; /* Remove the default focus outline */
}

.header button {
  width: 50px;
  height: 105px; 
  border: none;
  border-bottom-right-radius: 8px; 
  border-top-right-radius: 8px;
  background-color: #fff;  
  margin-top: 11px;
  margin-left: -30px;
  padding-left: 10px;
  cursor: pointer;
  border: 3px solid rgb(255, 94, 0);
}

.header button:active {
  border-color: white !important; /* Change border color to white when button is clicked */
}
.todoList { 
  flex: 70%; 
  width: 100%;
  display: flex;
  align-items: center;
  padding-top: 50px;
  flex-direction: column;
}

.txt {
  margin-top: -80px;
  margin-right: 45px;
  font-size: medium;
  color: black;
  font-style: italic;
  
}
/* Style for the title */
.td1 {
  margin-left: 25px;
  text-align: center;
  margin-top: -55px;
  display: inline-block;
  font-size: 40px;
  font-weight: bold;
  color: #ff6600; /* Orange color */
  padding: 5px 10px; /* Add padding to create space around the text */
  border-radius: 15px; /* Apply rounded corners to the span */
  background-color: white; /* Background color to match the container */
}

.task {
  width: 500px;
  height: 50px;
  display: flex;
  color: white;
  margin: 15px;
}

.task .content {
   flex: 80%;
   height: 100%;
   display: flex;
   justify-content: center;
   align-items: center;
}

.task .content span {
  display: grid;
  place-items: center;
  border: 1px solid white;
  width: 100%;
  height: 100%;
  font-size: 18px;
  border-right: none;
  background-color: rgb(255, 110, 27);
}

.task button {
  flex: 20%;
  height: 100%;
  background-color: rgb(255, 255, 255);
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  border-color: rgb(255, 110, 27);
  border-width: 3px;
  color: rgb(255, 110, 27);
  cursor: pointer;
  font-weight: bold;
  font-size: large;
}

.task button:active {
  border-color: white; /* Change border color to white when button is clicked */
}


